// Detail 컴포넌트 export

import { useContext, useEffect, useState } from "react";
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from "react-router-dom";
import $ from 'jquery';
import Nav from 'react-bootstrap/Nav';
import './../App.css';
import {Context1} from '../App.js';   // context API 활용
import {addCart} from './../store/userSlice.js';

function Detail(props) {

    let dispatch = useDispatch()    // Cart.js로 요청 보냄

    let {재고} = useContext(Context1);  // 부모 state 가져옴

    let [number, setNumber] = useState(''); // input state 설정

    let [alarm, setAlarm] = useState(true); // 2초 alert창 state 설정

    let [tab, setTab] = useState(0);    // tab state 설정

    let [shoes, setShoes] = useState(props.shoes);  // 부모 shoes를 다시 저장

    // input에 onChange 됐을 때 number 변경하는 함수
    let onChange = (e) => {
        setNumber(e.target.value);
    }


    let { id } = useParams();   // useParams로 import
    let found = props.shoes.find((element) => { return element.id == parseInt(id) });   // url에 입력한 번호와 같은 번호를 가진 상품 찾기
    let [fade, setFade] = useState(''); // 페이지 로드시 transition 주기 위해 fade state 설정

    console.log(found);
    // mount, update 시 코드 실행해주는 useEffect : html 렌더링 후 동작, 어려운 연산, 서버에서 데이터 가져오는 작업, 타이머 할 때때
    useEffect(() => {
        let timer = setTimeout(() => {
            setAlarm(false)
        }, 2000)
        // useEffect 동작 전에 실행되는 코드
        return () => {
            clearTimeout(timer);
        }
    }, [])

    // number가 변경될 때마다 인풋에 입력한게 숫자인지 확인
    useEffect(() => {
        if (isNaN(number)) {
            alert('그러지마세요');
        }
    }, [number]);

    // 컴포넌트 로드 시 fade를 end로 변경
    useEffect(()=>{
        let timer = setTimeout(()=>{setFade('end')}, 100)
        return ()=>{
            clearTimeout(timer);
            setFade('');
        }
    }, [])

    // 로칼스토리지에 내가 본 물건 데이터 저장
    let getLocal = JSON.parse(localStorage.getItem('watched'));
    getLocal.push(found.id);
    let 중복제거 = [...new Set(getLocal)];
    localStorage.setItem('watched', JSON.stringify(중복제거));

    return (

        // 페이지 로드되면 transition
        <div className={`container start ${fade}`}>

            {/* 2초 이내 구매시 할인 */}
            {
                alarm == true
                    ? <div className="alert alert-warning">
                        2초 이내 구매시 할인
                    </div>
                    : null
            }

            {/* input 실시간 감지 */}
            <input onChange={onChange}></input>


            {/* 물건 상세 정보 */}
            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${found.id + 1}.jpg`} width="100%" />
                </div>
                <div className="col-md-6">
                    <h4 className="pt-5">{found.title}</h4>
                    <p>{found.content}</p>
                    <p>{found.price}</p>
                    <button className="btn btn-danger" onClick={()=>{dispatch(addCart(found))}}>주문하기</button>
                </div>
            </div>

            {/* 탭 */}
            <Nav variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link onClick={()=>{setTab(0)}} eventKey="link-0">버튼0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={()=>{setTab(1)}} eventKey="link-1">버튼1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={()=>{setTab(2)}} eventKey="link-2">버튼2</Nav.Link>
                </Nav.Item>
            </Nav>
            <TabContent tab={tab} shoes={shoes}></TabContent>
            
            {/* 최근 본 상품 내역 */}
            <div className='sub-bg'>최근 본 상품
                {
                    중복제거.map((elem, index)=> {
                        return <p key={index}>상품id : {elem}</p>
                    })
                }
            </div>

        </div>
    )
}
// tab content 컴포넌트
function TabContent(props) {

    let [fade, setFade] = useState(''); // 탭 버튼 누르면 transition 주기위해 fade state 설정
    let {재고} = useContext(Context1);  // 부모 state 가져옴


    // tab state에 변동 생기면 작동하는 코드
    useEffect(()=>{
        let timer = setTimeout(()=>{setFade('end')}, 100)
        return ()=>{
            clearTimeout(timer);
            setFade('');
        }
    }, [props.tab]);

    return <div className={`start ${fade}`}>
        {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][props.tab]}
    </div>
}

export default Detail;